* {
	margin: 0;
	padding: 0;
	border: 0;
	vertical-align: baseline;
	background: transparent;
	font-weight: normal;
	text-decoration: none;
	outline: none;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
    font-family: "baloo_paaji_2regular";
	font-size: 17px;
	list-style: none;
}


@font-face {
    font-family: "baloo_paaji_2regular";
    src: url("./components/assets/fonts/baloopaaji2-regular-webfont.woff2")
            format("woff2"),
        url("./components/assets/fonts/baloopaaji2-regular-webfont.woff")
            format("woff");
    font-weight: normal;
    font-style: normal;
	
}








